<template>
  <div class="login-wrapper">
    <van-cell-group>
      <van-cell>
        <van-field
          v-model="url"
          required
          clearable
          label=""
          placeholder=""
          size="large"
        />
      </van-cell>
      <van-cell>
        <van-field
          v-model="viewername"
          clearable
          label=""
          placeholder=""
          size="large"
        />
      </van-cell>
      <van-cell>
        <van-field
          v-model="viewertoken"
          clearable
          type="password"
          label=""
          placeholder=""
          size="large"
        />
      </van-cell>
      <van-cell>
        <van-button
          class="btn"
          type="info"
          size="large"
          @click="handleBtnClick(0)"
          >
        </van-button>
      </van-cell>
      <van-cell>
        <van-button
          class="btn"
          type="info"
          size="large"
          @click="handleBtnClick(1)"
          >
        </van-button>
      </van-cell>
    </van-cell-group>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from "vuex";

export default {
  name: "Index",
  data() {
    return {
      url: "",
      viewername: "",
      viewertoken: ""
    };
  },
  computed: {
    ...mapState({
      currentUrl: "url"
    }),
    ...mapGetters(["getParamas"])
  },
  methods: {
    ...mapMutations(["changeUrl"]),
    handleBtnClick(type) {
      const name = this.viewername;
      const token = this.viewertoken;
      const _type = type;
      const params = `&viewername=${name}&viewertoken=${token}&type=${_type}`;
      const url = this.url + params;
      this.changeUrl(url);
      const query = this.getParamas;
      this.$router.push({ path: "live", query: query });
    }
  },
  mounted() {
    this.url = this.currentUrl;
  }
};
</script>

<style lang="stylus" scoped>
@import '~styles/mixins.styl'

.login-wrapper
  position absolute
  wrapper()
  background-color $ddd
</style>
